<template>
  <div class="login">
    <div class="content">
      <h1>大学生成绩管理系统</h1>
      <el-form :model="loginForm" status-icon :rules="rules" ref="loginForm"
               class="demo-ruleForm" style="text-align: center">
        <el-form-item prop="id">
          <el-input v-model="loginForm.id" autocomplete="off" placeholder="请输入学号/班主任编号/管理员编号"
                    clearable></el-input>
        </el-form-item>

        <el-form-item prop="password">
          <el-input show-password v-model="loginForm.password" autocomplete="off" placeholder="请输入密码(非管理员初始为身份证后6位)"
                    clearable></el-input>
        </el-form-item>

        <el-form-item prop="state">
          <el-select clearable placeholder="请选择身份" v-model="loginForm.state">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.label">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button style="width:145px" type="primary" @click="submitForm('loginForm')" round :disabled="ifDisabled">登录</el-button>
          <el-button style="width:145px" @click="resetForm('loginForm')" round>重置</el-button>
        </el-form-item>
        <el-form-item>
          <el-button style="width:300px" type="primary" round @click="toRegister">管理员注册</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import service from "@/request";

export default {
  name: "Login",
  data() {
    return {
      loginForm: {
        id: "",
        password: "",
        state: ""
      },
      options: [{
        value: '1',
        label: '学生'
      }, {
        value: '2',
        label: '班主任'
      }, {
        value: '3',
        label: '管理员'
      }],
      rules: {
        id: [
          {required: true, message: '编号不能为空', trigger: 'blur'},
          // {min: 2, max: 4, message: '姓名长度为2到4个字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '密码不能为空', trigger: 'blur'},
          {min: 6, max: 16, message: '密码长度为6到16个字符', trigger: 'blur'}
        ],
        state: [
          {required: true, message: '请选择身份', trigger: 'blur'}
        ]
      },
      ifDisabled:null
    }

  },
  methods: {
    toRegister() {
      this.$router.push('/addAdmin');
    },
    submitForm(loginForm) {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          axios.post(service.baseURL+"/login?id=" + this.loginForm.id + "&password=" +
              this.loginForm.password + "&state=" + this.loginForm.state,)
              .then((resp) => {
                if (resp.data.code == '20000') {
                  this.$message.success(resp.data.msg);
                  this.$store.commit('SET_TOKEN', resp.data.data.token)//存token
                  this.$store.commit('SET_USERINFO', resp.data.data.user)//存user
                  if (resp.data.data.state == '管理员') {
                    this.ifDisabled=true
                    this.$router.push('/admin')//后端
                  } else if (resp.data.data.state == '学生') {
                    this.ifDisabled=true
                    this.$router.push('/student')//后端
                  } else {
                    this.ifDisabled=true
                    this.$router.push('/teacher')//后端
                  }
                  this.$refs['loginForm'].resetFields();//成功才清空，方便输入正确的
                } else {
                  this.ifDisabled=false
                  this.$message.error(resp.data.msg)
                }
              }).catch(() => {
          })
        } else {
          return false;
        }
      })
    },
    resetForm(loginForm) {
      this.$refs.loginForm.resetFields();
    }
  }
}
</script>

<style scoped>
.login{
  background-image: url("../assets/img/login.webp");
  background-size: 100%;
  height: 100%;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
.content{
  text-align: center;
}
h1{
  color: #a52a47;
}
.el-form {
  width: 300px;
}
.el-form-item .el-select {
  width: 300px;
}

</style>